import React from 'react'

const UserIconContainer = (props) => {
    const {user, picture = true, name = false, date = false } = props;
    return (
       
                <>
                    { user && 
                    <div>
                        { picture && <img src={`/media/users/${user.id}.png`} title={user.name} />}
                        { name && <div className="name">{user.name}</div>}
                        { date && <div className="date">{user.date}</div>}
                    </div>
                    }
                    <style jsx>
                {`  

                    div {
                        display: flex;
                        align-items:center;
                        justify-content: center;
                    }

                   div img {
                        border-radius: 3px;
                        margin-right: 5px;
                   }

                   .name {
                        font: Bold 14px/17px Arial;
                        letter-spacing: 0;
                        color: #000000;
                    }

                    .date {
                        font: 10px/19px Arial;
                        letter-spacing: 0;
                        color: #707070;
                    }
                  
                `}

                </style>

                </>
              

    )
}

export default UserIconContainer;