import React from 'react'
import TplGlobal from './template-global'
import Users from '../blocks/users'

const TplProject = ({title, info, children}) => {

    return (
    

        <TplGlobal title={title}>
            <div className="main">
                <div className="header">
                    {title}
                </div>

                <div className="info">
                    By <div className="users">By <Users users={users}/></div>
                </div>

                <div className="content">
                    {children}
                </div>
            </div>

                <style jsx>
                {`  
                   
                   .main {
                     padding-left: var(--main-margin);
                    }
                   
                   .header {
                        align-items: center;
                        display: flex;
                        flex-direction: row;

                        
                        height: 60px;
                        background: #C2CCCC 0% 0% no-repeat padding-box;
                        border-radius: 0px 10px 5px 5px;
                        

                        font: Bold 22px/19px Arial;
                        letter-spacing: 0;
                        color: #000000;

                        padding-left: 4px;
                    }

                    .info {

                        align-items: center;
                        display: flex;
                        flex-direction: row;

                        width: 100%;
                        height: 60px;
                        
                        font: Bold 14px/17px Arial;
                        letter-spacing: 0;
                        color: #000000;
                    }
                `}

                </style>
        </TplGlobal>

        
    )
}

export default TplProject;