import React from 'react'
import UserIconContainer from '../icons/containers/user'


const Users = (props) => {
    const {users = [], picture, name, date} = props;
    
    return (
       <>
           <div className="users">{
                users.map((user, idx) => {
                    return <UserIconContainer key={`user_${user}`} picture={picture} name={name} date={date}  user={user} />
                })
            } </div>
                   

            <style jsx>
                {`  
                    
                .users {
                        align-items: center;
                        display: flex;
                        flex-direction: row;
                    }
                  
                `}

            </style>


        </>

    )
}

export default Users;